// style.scss

:root {
	--cta-bg: #ffffff; /* Clean white */
	--cta-text: #1e1e1e;
	--cta-accent: #00e676; /* Neon green */
	--cta-radius: 2rem;
	--cta-shadow: 0 12px 50px rgba(0, 0, 0, 0.1);
	--cta-border: 1px solid rgba(0, 230, 118, 0.25);
	--cta-bg-image: linear-gradient(
		135deg,
		rgba(255, 255, 255, 1) 0%,
		rgba(245, 255, 250, 1) 100%
	);
	text-transform: capitalize;
}

.cta-form-wrapper {
	background: var(--cta-bg-image);
	color: var(--cta-text);
	border-radius: var(--cta-radius);
	box-shadow: var(--cta-shadow);
	border: var(--cta-border);
	padding: 3rem 2.5rem;
	max-width: 600px;
	margin: 4rem auto;
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
	font-family: "Inter", sans-serif;
	text-align: center;
	animation: fadeIn 0.5s ease-in-out;
	position: relative;
	overflow: hidden;
	background-attachment: fixed;
}

.cta-form-wrapper::before {
	content: "";
	position: absolute;
	top: -1px;
	left: -1px;
	right: -1px;
	bottom: -1px;
	border: 2px solid transparent;
	border-radius: var(--cta-radius);
	background: linear-gradient(60deg, #00e676, #00b0ff, #e040fb);
	background-size: 600% 600%;
	animation: animatedBorder 8s ease infinite;
	z-index: 0;
	mask:
		linear-gradient(#fff 0 0) content-box,
		linear-gradient(#fff 0 0);
	mask-composite: exclude;
	-webkit-mask-composite: destination-out;
	padding: 2px;
	pointer-events: none;
}

.cta-form-wrapper > * {
	position: relative;
	z-index: 1;
}

.cta-form-wrapper h2 {
	font-size: 2.1rem;
	margin-bottom: 0.25rem;
	color: var(--cta-text);
}

.cta-form-wrapper p {
	font-size: 1.05rem;
	opacity: 0.6;
	margin-bottom: 1rem;
}

.cta-form-wrapper input,
.cta-form-wrapper select {
	background: #ffffff;
	color: #1e1e1e;
	border: 1px solid rgba(0, 0, 0, 0.12);
	padding: 0.85rem 0.5rem 0.85rem 0.5rem;
	margin-bottom: 10px;
	border-radius: 0.65rem;
	width: 100%;
	font-size: 1rem;
	text-transform: capitalize;
	transition:
		border-color 0.2s ease,
		box-shadow 0.2s ease;
}

.cta-form-wrapper input:focus,
.cta-form-wrapper select:focus {
	border-color: var(--cta-accent);
	box-shadow: 0 0 0 3px rgba(0, 230, 118, 0.25);
	outline: none;
}

.cta-form-wrapper button {
	background-color: var(--cta-accent);
	color: #fff;
	border: none;
	border-radius: 0.65rem;
	padding: 1rem 1.5rem;
	font-weight: 600;
	font-size: 1.05rem;
	cursor: pointer;
	transition:
		background-color 0.25s ease,
		transform 0.2s ease;
	box-shadow: 0 6px 20px rgba(0, 230, 118, 0.25);
}

.cta-form-wrapper button:hover {
	background-color: #00c853;
	transform: translateY(-2px);
}

@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translateY(30px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes animatedBorder {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
